export const get_modal_2 = () => {
  // Get the modal
  const modal = document.getElementById("block_students_myModal");
  const btn = document.getElementById("block_students_myBtn");
  const span = document.getElementsByClassName("block_students_close")[0];

  // When the user clicks the button, open the modal
  btn.onclick = function() {
    modal.style.display = "block";
  };

  // When the user clicks on <span> (x), close the modal
  span.onclick = function() {
    modal.style.display = "none";
  };
};